/*COLOR VARIABLES*/
$lightyellow: #fbf2d4;
$brightyellow: #f7c92a;
$mainpurple: #2a1150;
$lightpurple: #a5add2;

/*MODULARSCALE VARIABLE*/
$ratio: 1.333em;

/*FONTS*/
@font-face { /*title*/
  font-family: sunflora;
  src: url("../fonts/Sunflora.otf");
  font-weight: bold;
}
/*body*/
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');



/*GENERAL DOCWEB STYLES*/

html {
  margin:auto;
  font-size: 21.33px;
  font-family: Montserrat,sans-serif;
  line-height: $ratio;
  background: $lightyellow;  
}

h1 {
    font-size: 3.157em;
}

h2 {
    font-size: 1.777em;
}

/*class-based styling*/

.sunflora {
    font-family: sunflora;
}

.purple {
    color:$mainpurple;
}

.brightyellow {
    color: $brightyellow;
}



/*div-based stlying*/

/*coming soon page*/
#coming-soon-text {
    margin: auto;
    margin-top: 25vh;

    width: fit-content;
    padding-top: 1em;
    padding-bottom: 1em;
    padding-left: 4em;
    padding-right: 4em;


    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;

    line-height: .1em;
}

/*id-based styling*/
#see-you-text {
    margin-top:2em;
    font-weight: 400;
}

#coming-soon-heading {
    margin-left: 1.5em;
    font-weight: bold;
}
#coming-soon-img {
    margin-top: 3em;
    margin-left: 2em;
    width: 500px;
    z-index: -1;

    transform: rotate(-3deg);
}